"use client";

import { useState, useEffect, useRef } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { Video, VideoOff } from "lucide-react"; // ðŸ‘ˆ icons
import { cn } from "@/lib/utils";
import { vapi } from "@/lib/vapi.sdk";
import { interviewer } from "@/constants";
import { createFeedback } from "@/lib/actions/general.action";

enum CallStatus {
  INACTIVE = "INACTIVE",
  CONNECTING = "CONNECTING",
  ACTIVE = "ACTIVE",
  FINISHED = "FINISHED",
}

interface SavedMessage {
  role: "user" | "system" | "assistant";
  text: string;  // Add a text field to hold the message content
}

const Agent = ({
  userName,
  userId,
  interviewId,
  feedbackId,
  type,
  questions,
}: AgentProps) => {
  const router = useRouter();
  const [callStatus, setCallStatus] = useState<CallStatus>(CallStatus.INACTIVE);
  const [messages, setMessages] = useState<SavedMessage[]>([]);  // Keep track of messages
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [lastMessage, setLastMessage] = useState<string>("");  // Keeps track of the last message
  const [cameraOn, setCameraOn] = useState(false);

  const videoRef = useRef<HTMLVideoElement>(null);
  const streamRef = useRef<MediaStream | null>(null);

  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ video: true });
      streamRef.current = stream;
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
      }
    } catch (error) {
      console.error("Error accessing camera:", error);
    }
  };

  const stopCamera = () => {
    if (streamRef.current) {
      streamRef.current.getTracks().forEach((track) => track.stop());
      streamRef.current = null;
    }
    if (videoRef.current) {
      videoRef.current.srcObject = null;
    }
  };

  const toggleCamera = () => {
    if (cameraOn) {
      stopCamera();
    } else {
      startCamera();
    }
    setCameraOn(!cameraOn);
  };

  useEffect(() => {
    return () => {
      stopCamera();
    };
  }, []);

  const handleCall = async () => {
    setCallStatus(CallStatus.CONNECTING);
    if (type === "generate") {
      await vapi.start(process.env.NEXT_PUBLIC_VAPI_WORKFLOW_ID!, {
        variableValues: {
          username: userName,
          userid: userId,
        },
      });
    } else {
      let formattedQuestions = "";
      if (questions) {
        formattedQuestions = questions.map((question) => - `${question}`).join("\n");
      }
      await vapi.start(interviewer, {
        variableValues: {
          questions: formattedQuestions,
        },
      });
    }
    setCallStatus(CallStatus.ACTIVE);  // Set status to active once the call has started
  };

  const handleDisconnect = () => {
    setCallStatus(CallStatus.FINISHED);  // Set call status to finished
    vapi.stop();
    stopCamera();  // Make sure to stop the camera when disconnecting
  };

  // Add a function to handle message updates (simulating incoming messages)
  const handleNewMessage = (message: string, role: "user" | "system" | "assistant") => {
    const newMessage = { role, text: message };
    setMessages((prevMessages) => [...prevMessages, newMessage]);
    setLastMessage(message);  // Update the last message
  };

  return (
    <>
      <div className="call-view">
        <div className="card-interviewer">
          <div>
            <Image
              src="/logo2.png"
              alt="profile-image"
              width={65}
              height={54}
              className="object-cover"
            />
            {isSpeaking && <span className="animate-speak" />}
          </div>
          <h3>AI-Interviewer</h3>
        </div>

        <div className="card-border">
          <div className="card-content relative flex flex-col items-center gap-4">
            {/* Profile Picture / Video */}
            {cameraOn ? (
              <video
                ref={videoRef}
                autoPlay
                muted
                playsInline
                className="rounded-2xl object-cover size-[250px]"
              />
            ) : (
              <Image
                src="/userProfile.jpg"
                alt="profile-image"
                width={600}
                height={600}
                className="rounded-full object-cover size-[160px]"
              />
            )}
            <h3>{userName}</h3>

            {/* Camera Toggle Icon Button */}
            <button
              onClick={toggleCamera}
              className="absolute bottom-2 right-2 bg-blue-600 hover:bg-blue-700 text-white p-2 rounded-full"
            >
              {cameraOn ? <VideoOff size={20} /> : <Video size={20} />}
            </button>
          </div>
        </div>
      </div>

      {/* Transcript Section - Show subtitles here */}
      {messages.length > 0 && (
        <div className="transcript-border">
          <div className="transcript">
            {messages.map((message, index) => (
              <p
                key={index}
                className={cn(
                  "transition-opacity duration-500 opacity-0",
                  "animate-fadeIn opacity-100"
                )}
              >
                {message.text}
              </p>
            ))}
          </div>
        </div>
      )}

      {/* Call / End Call Button */}
      <div className="w-full flex justify-center mt-8">
        {callStatus !== CallStatus.ACTIVE ? (
          <button className="relative btn-call" onClick={handleCall}>
            <span
              className={cn(
                "absolute animate-ping rounded-full opacity-75",
                callStatus !== CallStatus.CONNECTING && "hidden"
              )}
            />
            <span className="relative">
              {callStatus === CallStatus.INACTIVE || callStatus === CallStatus.FINISHED
                ? "Call"
                : ". . ."}
            </span>
          </button>
        ) : (
          <button className="btn-disconnect" onClick={handleDisconnect}>
            End Call
          </button>
        )}
      </div>
    </>
  );
};

export default Agent;